@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@100;400;700&display=swap);
body {
  margin: 0;
  padding: 0;
  font-family: 'Roboto', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}


.dayLight{
  background-color: rgba(230, 214, 217, 0.8);
  --color-primary: #3B3B3B;
  --color-secundary: #888888;
  --color-third: #888888;
  color: var(--color-secundary);
}

.dayDark{
  background-color: rgba(113, 130, 166, 0.8);
  --color-primary: #050042;
  --color-secundary: #fff;
  --color-third: #514E76;
  color: var(--color-secundary);
}

.day {
  border-radius: 10px;
  padding: 15px;
  position: relative;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
}

.day small {
  z-index: 2;
  color: var(--color-primary)
}

.day img{
  height: 50px;
  width: 50px;
  position: absolute;
  top: 25%;
  left: 50%;
  z-index: 0;
}

.day h2{
  font-size: 50px;
  font-weight: normal;
  margin: auto;
  z-index: 1;
  color: var(--color-primary);
}

.containerFranceLight{
  background-color: rgba(230, 214, 217, 0.8);
  --color-primary: #3B3B3B;
  --color-secundary: #888888;
  color: var(--color-primary);
}

.containerFranceDark{
  background-color: rgba(113, 130, 166, 0.8);
  --color-primary: #050042;
  --color-secundary: #fff;
  color: var(--color-primary);

}

.containerFrance {
  grid-area: france;
  border-radius: 10px;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}

.containerFrance__information{
  text-align: center;
}

.containerFrance h2{
  color: --color-primary;
  font-size: 2rem;
}

.information__city p{
  
  color: var(--color-secundary);
}
.containerFrance__imgRound{
  height: 6rem;
  width: 6rem;
  border-radius: 50%;
  margin: 10px;
}

.containerFrance__temperature p{
  font-size: 4rem;
  display: inline-block;
}
.containerFrance__temperature img{
  height: 50px;
  width: 50px;
}

.containerFrance__text {
  width: 70%;
  text-align: right;
  color: var(--color-secundary);
}

@media screen and (max-width: 880px) {
  .containerFrance{
    padding: 15px;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-justify-content: space-between;
            justify-content: space-between;
  }

  .containerFrance__text {
    display: none;
  }

  .containerFrance__information{
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row-reverse;
            flex-direction: row-reverse;
    text-align: left;
  }

  .containerFrance__temperature{
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column-reverse;
            flex-direction: column-reverse;
    -webkit-align-items: flex-end;
            align-items: flex-end;
  }
}
.container__mainLight{
  --color-primary: #3B3B3B;
  --color-secundary: #888888;
  color: var(--color-primary);
}
.container__mainDark{
  --color-primary: #050042;
  --color-secundary: #514E76;
  color: var(--color-primary);
}
.container__main{
  grid-area: left;
  margin-left: 1.5rem;
}

.container__main--temperature img{
  height: 80px;
  width: 80px;
}

.gradeNumber{
  display: inline-block;
  margin: 0;
  font-size: 9rem;
  font-weight: 700;
}

.container__main--city{
  font-size: 2rem;
  font-weight: 400;
  color: var(--color-secundary);
  -webkit-margin-before: 0;
          margin-block-start: 0;
  -webkit-margin-after: 0;
          margin-block-end: 0;
  padding: 0;
}

.container__main--country{
  font-size: 3rem;
  font-weight: 700;
}
.container__main--date{
  color: var(--color-secundary);
  font-size: 1rem;
  font-weight: 400;
  
}

@media screen and (max-width: 880px) {
  .container__main{
    background-color: rgba(255, 255, 255, 0.5);
    border-radius: 10px;
    padding: 15px;
    text-align: center;
    margin-left: 0;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: center;
            justify-content: center;
  }

  .gradeNumber{
    font-size: 4rem;
  }
  
  .container__main--city{
    font-size: 1.5rem;
  }

  .container__main--country{
    font-size: 2rem;
  }

  .container__main--date{
    font-size: 0.8rem;
    
  }

  .container__main--temperature {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column-reverse;
            flex-direction: column-reverse;
  }

  .container__main--temperature img{
    display: block;
    margin: auto;
  }
}

*{
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: Roboto, 'Open Sans', 'Helvetica Neue', sans-serif;
}

.backgroundLight{
  --background: url('https://images.unsplash.com/photo-1433838552652-f9a46b332c40?ixid=MXwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHw%3D&ixlib=rb-1.2.1&auto=format&fit=crop&w=750&q=80') 
  no-repeat center center fixed;
  background: var(--background);
  background-size: cover;
}

.backgroundDark{
  --background: url('https://images.unsplash.com/photo-1517124686104-3830fbe48de6?ixid=MXwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHw%3D&ixlib=rb-1.2.1&auto=format&fit=crop&w=667&q=80') 
  no-repeat center center fixed;
  background: var(--background);
  background-size: cover;
}

.layer {
  height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;

}
.layer__container{
  background-color: rgba(255, 255, 255, 0.5);
  border-radius: 15px;
  height: 85vh;
  width: 85vw;
  display: grid;
  grid-template-columns: 3fr 1fr 1fr 1fr;
  grid-template-rows: 1fr 32%;
  grid-template-areas: 
    "left france france france"
    "left day1 day2 day3";
  grid-gap: 1rem;
  padding: 1rem;
}

@media screen and (max-width: 880px) {
  .layer {
    height: 100vh;
    width: 100%;
    padding: 15px;
  }

  .layer__container{
    background-color: transparent;
    box-sizing: border-box;
    border-radius: 15px;
    height: 100%;
    width: 100%;
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: 2fr 1fr 1fr 1fr;
    grid-template-areas: 
      "left left"
      "france france"
      "day1 day2"
      "day1 day3";
    grid-gap: 1rem;
    padding: 0;
    margin-top: 0;
  }



}


